/* ===================================
1. General
==================================== */

body {
    font-family: 'Open Sans', Arial, sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 1.6em;
    color: #656565;
}

a:active {
    outline: 0;
}

h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    color: $headings-color;
}

.clear {
    clear: both;
}

/* ===================================
2. layout
==================================== */

.container {
    padding: 0 20px 0 20px;
    position: relative;
}

#wrapper {
    width: 100%;
    margin: 0;
    padding: 0;
}

#wrapper.boxed {
    width: 1230px;
    overflow: hidden;
    margin: 0 auto;
    padding: 0;
    background: #fff;
}

.row, .row-fluid {
    margin-bottom: 30px;
}

.row .row, .row-fluid .row-fluid {
    margin-bottom: 30px;
}

.row.nomargin, .row-fluid.nomargin {
    margin-bottom: 0;
}

/* ===================================
3. Responsive media
==================================== */

.video-container, .map-container, .embed-container {
    position: relative;
    margin: 0 0 15px 0;
    padding-bottom: 51%;
    padding-top: 30px;
    height: 0;
    overflow: hidden;
    border: none;
}

.embed-container iframe, .embed-container object, .embed-container embed, .video-container iframe, .map-container iframe, .map-container object, .map-container embed, .video-container object, .video-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

iframe {
    border: none;
}

img.img-polaroid {
    margin: 0 0 20px 0;
}

.img-box {
    max-width: 100%;
}

/* ===================================
4. Header
==================================== */

/* --- hidden top area --- */

.hidden-top {
    height: 0;
    color: #fff;
    overflow: hidden;
    -webkit-transition: all 0.4s ease-out;
    -moz-transition: all 0.4s ease-out;
    -o-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
}

.hidden-top-inner ul {
    list-style: none;
    margin-top: 30px;
    margin-left: 0;
}

.hidden-top-inner ul li {
    display: inline;
    margin: 0 20px 0 0;
}

.hidden-top.toggle-link-open {
    padding: 10px 0;
}

.hidden-top a {
    color: #ffffff;
}

#header-hidden-link {
    position: absolute;
    right: 60px;
    top: 0px;
}

#header-hidden-link a.toggle-link, #header-hidden-link a.top-link {
    outline: 0;
    font-size: 0;
    line-height: 1;
    padding: 5px 10px;
    height: 20px;
    display: block;
    text-align: center;
    color: #fefefe;
    text-decoration: none;
}

#header-hidden-link a.toggle-link:active, #header-hidden-link a.toggle-link:focus, #header-hidden-link a.toggle-link:hover {
    outline: 0;
}

#header-hidden-link a.toggle-link i, #header-hidden-link a.top-link i {
    font-size: 16px;
    line-height: 1;
}

#header-hidden-link a.toggle-link:hover, #header-hidden-link a.top-link:hover {
    text-decoration: none;
    color: #ffffff;
}

#header-hidden-link a.toggle-link > i:before {
    content: "\f067";
    font-family: 'FontAwesome';
    font-size: 13px;
    font-weight: normal;
    font-style: normal;
}

#header-hidden-link a.toggle-link.open {
    text-decoration: none;
}

#header-hidden-link a.toggle-link.open > i:before {
    content: "\f068";
}

.toggle-link-open {
    height: auto !important;
    min-height: 40px;
    max-height: 40000px;
}

.toggle-link-close {
    height: 0;
    overflow: hidden;
}

/* --- modal signup & signin form --- */

/* modal styled */

.modal.styled .modal-header {
    background-color: #F03C02;
}

.modal.styled .modal-header h4 {
    color: #fff;
}

/* --- header -- */

header {
    background: #fff;
    margin: 0;
    padding: 0;
    position: relative;
}

header .logo {
    margin: -10px 0 0 0;
    float: left;
}

header .logo img {
    margin-bottom: 10px;
}

header .logo h1 {
    font-size: 24px;
    margin: 0;
    font-weight: 500;
}

header .logo .logo1 {
    color: $gray-dark;
}

header .logo .logo2 {
    color: $theme-color-1;
}

header .logo h2 {
    font-size: $font-size-medium;
    color: $gray-light
}

/* --- headnav menu : signup & signin --- */

.headnav {
    float: right;
    margin: 35px 0 0 0;
    border-bottom: 1px dotted #e6e6e6;
}

.headnav ul {
    margin-left: 0;
    list-style: none;
}

.headnav ul li {
    display: inline;
    margin-left: 10px;
}

.headnav ul li a {
    outline: 0;
    font-size: 12px;
    font-weight: 600;
}

.headnav ul li a:hover, .headnav ul li a:focus, .headnav ul li a:active {
    outline: 0;
}

/* --- menu --- */

@media (min-width: 768px) {
    header .navigation {
        float: right;
    }
}

header ul.navbar-nav li {
    border: none;
    margin: 0;
}

header ul.navbar-nav li a {
    font-size: 12px;
    border: none;
    font-weight: 300;
    text-transform: uppercase;
}

.navbar .navbar-nav .nav > li > a {
    color: #111;
    text-shadow: none;
}



.navbar .navbar-nav a.section {
    color: $navbar-default-link-color;
}

.navbar-light .navbar-nav .nav-link:hover, .navbar-light .navbar-nav .nav-link.current {
    color: $navbar-default-link-hover-color;
}

.navbar-light .navbar-nav .nav-link.current, .navbar-light .navbar-nav .nav-link.section  {
    font-weight: bold;
}


.navbar .navbar-nav .nav a:hover {
    background: none;
}

.navbar .navbar-nav .nav > .active > a, .navbar .nav > .active > a:hover {
    background: none;
    font-weight: 600;
}

.navbar .navbar-nav .nav > .active > a:active, .navbar .nav > .active > a:focus {
    background: none;
    outline: 0;
    font-weight: 600;
}

.navbar .navbar-nav .nav li .dropdown-menu {
    z-index: 2000;
}

.dropdown-menu a {
    background: black;
    color: #ddd;
}

.dropdown-menu a.current {
    color: $dropdown-link-color;
}

.dropdown-menu a:hover {
    //background-color: $theme-color-1;
}

header ul.navbar-nav li ul {
    margin-top: 1px;
}

header ul.navbar-nav li ul li ul {
    margin: 1px 0 0 1px;
}

.dropdown-menu .dropdown i {
    position: absolute;
    right: 0;
    margin-top: 3px;
    padding-left: 20px;
}

.navbar .navbar-nav > li > .dropdown-menu:before {
    display: inline-block;
    border-right: none;
    border-bottom: none;
    border-left: none;
    border-bottom-color: none;
    content: none;
}

.navbar .navbar-nav > li > .dropdown-menu:after {
    display: inline-block;
    border-right: none;
    border-bottom: none;
    border-left: none;
    content: '';
}

ul.navbar-nav li.dropdown a {
    z-index: 1000;
    display: block;
}

select.selectmenu {
    display: none;
}

