/* ============== options panel for live preview ======================= */

#t_options {
    position: fixed;
    top: 30px;
    left: -230px;
    width: 230px;
    background: #111;
    color: #fff;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.12);
    box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
    -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
    -moz-box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
    -o-box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
    z-index: 5000;
    font-size: 11px;
    font-family: 'Open Sans', arial, sans-serif;
}

#t_options hr {
    margin: 20px 0;
    border: 0;
    border-top: none;
    border-bottom: 1px dotted #999;
}

#t_options h6 {
    color: #fff;
    font-size: 14px;
}

#t_options input {
    width: 60px;
}

#t_options .col {
    width: 50px;
    margin: 0 10px 0;
}

.colorpicker {
    z-index: 5001;
}

.options_inner {
    position: relative;
    width: 100%;
    height: 100%;
}

.options_box {
    margin: 0 14px;
    padding: 10px 0 20px;
    overflow: hidden;
}

.options_box .solidline {
    margin-bottom: 10px;
}

.options_box h6 {
    text-align: center;
    font-weight: bold;
}

.options_box .full {
    width: 100%;
    overflow: hidden;
    padding-bottom: 10px;
}

.options_box .halfcol {
    width: 90px;
    float: left;
    margin: 0 5px;
}

.options_box .fourcol {
    width: 30px;
    float: left;
    margin: 0 5px;
}

.options_box .fourcol.color a {
    width: 30px;
    height: 30px;
    display: block;
    margin-bottom: 5px;
}

.options_box .halfcol .wideboxed h6 {
    font-size: 11px;
    font-weight: 600;
}

.options_toggle_holder {
    position: absolute;
    right: -40px;
}

.options_toggle {
    margin: 6px 0 0 4px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: 1px solid #111;
    border-left: none;
    display: block;
    background: #111;
}

.options_toggle span .icon {
    margin: 10px 0 0 7px;
    display: block;
}

.options_toggle:hover, .options_toggle:focus, .options_toggle:active {
    outline: 0;
    text-decoration: none;
}

.options_heading {
    color: #fff;
    margin-top: 5px;
    margin-bottom: 20px;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
    padding-bottom: 10px;
    border-bottom: 1px dotted #888;
}

/* colors */

.options_box a.t_default {
    background: #ef5523;
}

.options_box a.t_kissme {
    background: #6D0C1F;
}

